
* {
    padding: 0;
    margin: 0;
}
html,body {
    background: #fcfcfc;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    font-family: '\5FAE\8F6F\96C5\9ED1';
}

#player {
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 50px;
    background: rgb(247, 247, 221);
    bottom: 0;
    color: #4d4b4b;
}
.player-content {
    width: 40px;
    height: 40px;
    margin: 5px 0 0 10px;
    position: relative;
}
.song-title,.single-source {
    display: inline-block;
    position: absolute;
    top: 5px;
    left: 70px;
    font-size: 14px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    max-width: 240px;
}
.player-content img {
    width: 100%;
    height: 100%;
    -webkit-box-shadow: 0 3px 3px rgba(73, 73, 68, 0.596);
            box-shadow: 0 3px 3px rgba(73, 73, 68, 0.596);
    display: inline-block;
}
.single-author {
    margin-top: 5px;
    font-size: 13px;
}
.single-source {
    font-size: 12px;   
    top: 25px;
}
.icon-zanting,.icon-bofang {
    position: absolute;
    font-size: 40px;
    top: 50%;
    right: 5px;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.header {
    width: 100%;
    height: 64px;
    background: #d43c33;
    line-height: 64px;
    position: fixed;
    top: 0;
    z-index: 1;
}
.header p {
    font-size: 19px;
    font-weight: bold;
    color: #fff;
    display: inline-block;
    padding: 0 0 0 25px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.icon-wxbsousuotuiguang {
    float: right;
    margin-right: 15px;
    color: #fff;
    font-weight: bold;
}
::-webkit-scrollbar{
display:none;
}
* {
    padding: 0;
    margin: 0;
}
html,body {
    background: #fcfcfc;
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    font-family: '\5FAE\8F6F\96C5\9ED1';
}
#fourScreen {
    margin-top: 124px;
    padding: 10px;
    color: #D3BD74;
    line-height: 25px;
    font-size: 15px;
    text-indent: 5px;
}
a:link,a:visited,a:hover,a:active {
    text-decoration: none;
} 

.tab {
    width: 100%;
    height: 40px;
    background: #fff;
    border-bottom: 2px solid #ede9e9;
    position: fixed;
    top: 64px;
    z-index: 1;
}
.tab li {
    width: 25%;
    display: inline-block;
    text-align: center;
    line-height: 40px;
    font-size: 13px;
    color: #363652;
    cursor: pointer;
    list-style: none;
    font-weight: 500;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.active {
    border-bottom: 2px solid #ef4136;
    color: #d33a31;
}

#recommended {
    margin-top: 124px;
}
.recommended-nav {
    width: 100%;
    height: 20px;
    margin: 20px 0 15px 0;
    border-left: 2px solid #d33a31;
    line-height: 18px;
}
.recommended-nav p {
    text-indent: 10px;
    font-size: 17px;
}
.recommended-content {
    width: 100%;
    height: 350px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.recommended-content li {
    width: 30%;
    height: 158px;
    margin: 0 auto;
    list-style: none;
    padding-bottom: 16px;
}
.recommended-content li img {
    width: 122px;
    height: 122px;
    -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, .4);
            box-shadow: 0 2px 3px rgba(0, 0, 0, .4);
}
.recommended-content li p {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 4px 2px 0 6px;
    line-height: 1.2;
    font-size: 12px;
    color: #333;
    line-height: 15px;
}

#lateset_nav {
    margin: 30px 0 10px 0;
}
.latest-list {
    font-family: '\5FAE\8F6F\96C5\9ED1';
    padding: 6px 0 6px 10px;
    position: relative;
    border-bottom: 1px solid rgba(189, 188, 188, 0.2);
}
.latest-list p {
    font-size: 17px;
    color: #333;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    max-width: 90%;
}
.latest-list span {
    font-size: 12px;
    color: #888;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    max-width: 80%;
    display: inline-block;
}
.icon-iconset0481 {
   position: absolute;
   right: 20px;
   top: 15px;
   font-size: 20px;
}
.icon-bat2 {
    color: #d43c33;
    font-size: 12px;
} 

#hotSong {
    margin-top: 104px;
}
.coverImg {
    height: 160px;
    width: 100%;
    background: url(https://p1.music.126.net/GhhuF6Ep5Tq9IEvLsyCN7w==/18708190348409091.jpg)no-repeat;
    background-size: cover;
    background-position-y: -110px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: left;
        -ms-flex-pack: left;
            justify-content: left;
    -webkit-box-align: top;
        -ms-flex-align: top;
            align-items: top;
}
.coverImg-title {
    height: 30px;
    padding-left: 10px;
    padding-top:  5px;
    line-height: 25px;
}
.coverImg-title i {
    color: hsla(0,0%,100%,.8);
    font-size: 13px;
}
.coverImg-title p {
    color: #fff;
    font-size: 20px;
}

#search {
    margin-top: 104px;
}
#specialFont {
    font-size: 15px;
}
.m-input {
    padding: 15px 10px;
    border-bottom: 1px solid rgba(109, 108, 108, 0.1);
}
.input-cover {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #ebecec;
    border-radius: 30px;
}
.hot-search {
    padding: 15px 10px 0;
}
.hot-title {
    padding: 0px 0 10px 7px;
    font-size: 13px;
    color: #666;
}
.hot-label {
    display: inline-block;
    height: 32px;
    margin-right: 8px;
    margin-bottom: 8px;
    padding: 0 14px;
    font-size: 14px;
    line-height: 32px;
    color: #333;
    border: 1px solid rgb(208, 205, 205);
    border-radius: 15px;
    cursor: pointer;
}
.m-input input {
    width: 100%;
    height: 30px;
    line-height: 18px;
    background: transparent;
    font-size: 14px;
    color: #333;
    border: none;
}
.m-input input:focus {
    outline:none;
}
.icon-search {
    position: absolute;
    color: #c2bfbf;
    font-size: 16px;
    left: 8px;
    top: 6px;
}

::-webkit-scrollbar{
display:none;
}
#Details {
    background:#f8f8f8;
}
.details-header {
    width: 100%;
    height: 186px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-size: cover;
    overflow: hidden;
}
.next-level {
    width: 90%;
    height: 126px;
    position: relative;
    z-index: 2;
}
.next-level p {
    padding: 1px 0 1px 15px;
    font-size: 17px;
    line-height: 1.3;
    color: #fefefe;
    height: 44px;
    display: -webkit-box;
}
.title-img {
    width: 126px;
    height: 126px;
    float: left;
}
.author-msg {
    display: inline-block;
    color: hsla(0,0%,100%,.7);
    font-size: 13px;
    height: 30px;
    margin: 20px 0 0 20px;
}
.author-msg img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    top: 9px;
}
.details-label {
    position: relative;
    margin: 10px 10px 0 15px;
    padding-top: 10px;
    line-height: 17px;
    color: #666;
    font-size: 12px;
    letter-spacing: 2px;
}
.label {
    display: block;
    padding: 5px 0;
}
.label div {
    display: inline-block;
    border: 1px solid rgb(224, 220, 220);
    padding: 3px;
    margin: 0 5px;
    border-radius: 10px;
}
.details-list-title {
    width: 100%;
    height: 23px;
    line-height: 23px;
    margin-top: 15px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
}  

